@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

.App {
  text-align: center;
  background-color: #121212;
  width: 100vw;
  height: 100vh;
  /* display: flex; */
  color: white;
}

body {
  padding: 0%;
  margin: 0;
}

nav {
  height: 60px;
  width: 100%;
  margin: 0;
  border-bottom: 1px solid grey;
  display: grid;
  place-items: center;
}

nav h1 {
  margin: 0;
  font-family: Poppins, sans-serif;
  color: white;
  font-size: 45px;
}
.game {
  width: 100vw;
  height: calc(100vh - 170px);
  display: flex;
  align-items: center;
  padding-top: 50px;
  flex-direction: column;
}

.board {
  width: 450px;
  height: 550px;
  border: 1px solid black;
  display: flex;
  flex-direction: column;
}

.row {
  flex: 33%;
  display: flex;
  flex-direction: row;
  margin: 5px;
}

.letter {
  flex: 33%;
  height: 100%;
  border: 1px solid grey;
  margin: 5px;
  display: grid;
  place-items: center;
  font-size: 30px;
  font-weight: bolder;
  color: white;
  font-family: Poppins, sans-serif;
}

#correct {
  background-color: #528d4e;
}

#almost {
  background-color: #b49f39;
}

#error {
  background-color: #3a393c;
}

.keyboard {
  width: 700px;
  height: 300px;
  margin-top: 60px;
}

.line1 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  display: flex;
  justify-content: center;
  margin: 5px;
}
.line2 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 5px;
}
.line3 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 5px;
}

.key {
  width: 50px;
  height: 70px;
  margin: 5px;
  border-radius: 4px;
  display: grid;
  place-items: center;
  font-size: 20px;
  background-color: grey;
  color: white;
  font-family: Poppins, sans-serif;
  cursor: pointer;
}

#big {
  width: 100px;
}
#disabled {
  background-color: #3a393c;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
}

/* Modal Content */
.modal-content {
  margin: 7.5% auto;
  text-align: center;
  font-family: Poppins, sans-serif;
  transition: ease-in-out 400ms;
}

.modal__p {
  color: #b49f39;
  font-weight: 600;
  transition: ease-in 1000ms;
}

h1,
h2,
h3 {
  margin: 0;
  font-family: Poppins, sans-serif;
  color: white;
}

.gameOver {
  padding: 2rem;
}

@media (max-height: 800px) {
  .board {
    width: 40%;
    height: 100%;
  }

  .keyboard {
    width: 95%;
    height: 60%;
  }

  .key {
    width: 2.5rem; /* 40px */
    height: 3.5rem; /* 56px */
    margin: 0.25rem; /* 4px */
    font-size: 1rem; /* 16px */
  }

  .letter {
    font-size: 1rem;
  }

  #big {
    width: 3.4rem; /* 80px */
  }

  nav h1 {
    font-size: 2rem; /* 32px */
  }

  .modal-content {
    width: 90%;
  }
}

@media (max-width: 700px) {
  .board {
    width: 90%;
    height: 100%;
  }

  .keyboard {
    width: 95%;
    height: 60%;
  }

  .key {
    width: 2.5rem; /* 40px */
    height: 3.5rem; /* 56px */
    margin: 0.25rem; /* 4px */
    font-size: 1rem; /* 16px */
  }

  .letter {
    font-size: 1.5rem;
  }

  #big {
    width: 3.4rem; /* 80px */
  }

  nav h1 {
    font-size: 2rem; /* 32px */
  }

  .modal-content {
    width: 90%;
  }
}



.restartButton {
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  font-family: Poppins, sans-serif;
  color: white;
  background-color: #528d4e;
  border: none;
  border-radius: 0.25rem; /* 4px */
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.restartButton:hover {
  background-color: #528d9f;
}

p {
  font-family: Poppins, sans-serif;
}
